<template>
  <PageBody>
    <Breadcrumb>
      <router-link tag="li" to="/wall">教练墙</router-link>
      <li>添加教练</li>
    </Breadcrumb>
    <div class="detail-form" v-loading="isGettingData">
      <div class="detail-item">
        <div class="detail-label">上传照片</div>
        <div class="detail-control">
          <Upload
            action="noAction"
            :before-upload="beforeUpload">
            <div class="picture-uploader">
              <img v-if="preview" :src="preview" alt="" class="preview-img">
              <div v-else>
                <i class="add-picture-icon"></i>
                <p style="color: #bdbcbc;margin-top: 10px">请上传3M内<br>大小的图片</p>
              </div>
            </div>
          </Upload>
        </div>
      </div>
      <div class="detail-item">
        <div class="detail-label">教练名称</div>
        <div class="detail-control">
          <Input placeholder="请输入教练名称" style="width: 200px" v-model="form.name" :maxlength="36"/>
        </div>
      </div>
      <div class="detail-item">
        <div class="detail-label">用户性别</div>
        <div class="detail-control">
          <Select style="width:200px" v-model="form.sex">
            <Option value="1">男</Option>
            <Option value="0">女</Option>
          </Select>
        </div>
      </div>
      <div class="detail-item">
        <div class="detail-label rate-label">星级评分</div>
        <div class="detail-control">
          <Rate class="rate-container" allow-half :value="Number(form.rank)/2" @on-change="val=>form.rank=val*2"/>
        </div>
      </div>
      <div class="detail-item">
        <div class="detail-label">个人简介</div>
        <div class="detail-control">
          <div class="detail-text-box">
            <Input type="textarea" placeholder="请输入个人简介" class="detail-text"
                   v-model="form.introduction" :maxlength="300"/>
            <i class="detail-text-num">{{300-form.introduction.length}}</i>
          </div>
        </div>
      </div>
      <div class="detail-item">
        <div class="detail-label">训练介绍</div>
        <div class="detail-control">
          <div class="detail-text-box">
            <Input type="textarea" placeholder="请输入训练介绍" class="detail-text"
                   v-model="form.trainContent" :maxlength="300"/>
            <i class="detail-text-num">{{300-form.trainContent.length}}</i>
          </div>
        </div>
      </div>
      <div class="detail-item">
        <div class="detail-label"></div>
        <div class="detail-control">
          <div style="display: inline-block" v-loading="isSubmitting">
            <button class="gym-btn border-btn" @click="()=>{$router.back()}">取消</button>
            <button :class="{'disable-confirm':JSON.stringify(form)===checkFormData}"
                    class="gym-btn primary-btn confirm-btn" @click="handleSubmit">确定
            </button>
          </div>
        </div>
      </div>
    </div>
  </PageBody>
</template>
<script>
import { addData, getDataById, updateDataById } from 'api/wall'
import Breadcrumb from 'views/components/Breadcrumb'
import { Upload } from 'element-ui'
import Validate from 'utils/validate'
import { Rate } from 'iview'

export default {
  components: {Breadcrumb, Upload, Rate},
  data () {
    return {
      preview: '',
      form: {
        image: '',
        name: '',
        sex: '1',
        rank: 10,
        introduction: '',
        trainContent: ''
      },
      isSubmitting: false,
      isGettingData: false,
      checkFormData: ''
    }
  },
  watch: {
    'form.image': function (value) {
      if (typeof value !== 'string') {
        let reader = new FileReader()
        reader.readAsDataURL(this.form.image)
        reader.addEventListener('load', () => {
          this.preview = reader.result
        }, {once: true})
      }
    }
  },
  mounted () {
    if (this.$route.params.id) {
      this.isGettingData = true
      getDataById({id: this.$route.params.id}).then(res => {
        if (Number(res.code) === 200) {
          this.isGettingData = false
          this.preview = res.data.image
          this.form.name = res.data.name
          this.form.sex = res.data.sex
          this.form.rank = res.data.rank
          this.form.introduction = res.data.introduction
          this.form.trainContent = res.data.trainContent
          this.checkFormData = JSON.stringify(this.form)
        } else {
          this.$message({
            message: res.message,
            type: 'error',
            onClose: () => {
              this.$router.back()
            }
          })
        }
      })
    }
  },
  methods: {
    beforeUpload (file) {
      let fileSize = {
        name: '3M',
        byte: 3 * 1024 * 1024,
        max: 30
      }
      if (['image/jpg', 'image/jpeg', 'image/png', 'image/bmp'].indexOf(file.type) === -1) {
        this.$message({
          message: '文件格式错误，请上传jpg,jpeg,png,bmp文件',
          type: 'warning'
        })
        return false
      } else if (file.size > fileSize.byte) {
        this.$message({
          message: '文件太大，请上传' + fileSize.name + '以下的文件',
          type: 'warning'
        })
        return false
      } else {
        this.form.image = file
        return false
      }
    },
    validate () {
      if (JSON.stringify(this.form) === this.checkFormData) {
        return false
      } else if (!this.preview) {
        this.$message({
          message: '请上传照片',
          type: 'warning'
        })
        return false
      } else if (Validate.isNullString(this.form.name)) {
        this.$message({
          message: '请输入教练名称',
          type: 'warning'
        })
        return false
      } else if (Validate.isNullString(this.form.introduction)) {
        this.$message({
          message: '请输入个人简介',
          type: 'warning'
        })
        return false
      }
      return true
    },
    handleSubmit () {
      if (this.validate()) {
        this.postData()
      }
    },
    getFormData () {
      let formData = new FormData()
      formData.append('gymAddr', this.$store.state.token)
      if (this.form.image) {
        formData.append('file', this.form.image)
      }
      formData.append('name', this.form.name)
      formData.append('sex', this.form.sex)
      formData.append('rank', this.form.rank)
      formData.append('introduction', this.form.introduction)
      formData.append('trainContent', this.form.trainContent)
      return formData
    },
    postData () {
      this.isSubmitting = true
      let formData = this.getFormData()
      let postAjax
      if (this.$route.params.id) {
        formData.append('id', this.$route.params.id)
        postAjax = updateDataById
      } else {
        postAjax = addData
      }
      postAjax(formData).then(res => {
        if (Number(res.code) === 200) {
          this.$router.back()
          this.$message({
            message: res.message,
            type: 'success'
          })
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      }).finally(() => {
        this.isSubmitting = false
      })
    }
  }
}
</script>
<style lang='stylus' scoped>
  .detail-form
    display flex
    flex-direction column
    align-items stretch
    padding-right 20px
  .detail-item
    min-height 36px
    display flex
    flex-direction row
    margin-bottom 20px
  .detail-label
    font-size 14px
    text-align right
    width 108px
    padding-right 20px
    position relative
    top 4px
  .detail-control
    flex 1
  .detail-text-box
    width 100%
    max-width 540px
    position relative
  .detail-text
    width 100%
  .detail-text /deep/ textarea.ivu-input
    font-size 12px
    resize none
    height 100px
  .detail-text-num
    position absolute
    bottom 0
    right 0
    z-index 1
    font-style normal
    width 26px
    height 14px
    display flex
    align-items center
    justify-content center
  .detail-text-num:before
    content ''
    position absolute
    bottom 1px
    right 1px
    z-index -1
    width 0
    height 0
    border-top 30px solid transparent
    border-right 40px solid #eee
  .picture-uploader
    width 108px
    height 128px
    background #F0F0F0
    border-radius 4px
    border 1px solid #D6D6D6
    display flex
    align-items center
    justify-content center
  .preview-img
    width 100%
    height 100%
  .add-picture-icon
    display inline-block
    background url('~@/assets/add.png') no-repeat center
    background-size 100% 100%
    width 26px
    height 26px
  .rate-container
    font-size 28px
  .rate-label
    padding-top 7px
  .confirm-btn
    margin-left 20px
    &.disable-confirm
      opacity 0.25
      cursor not-allowed
</style>



